@font-face {
    font-family: 'TT Normal';
    src: url('../fonts/TTNorms-Regular.ttf');
}


.header{
    width: 100%;
    height: auto;
    position: relative;
    &-bg{
        position: absolute;
        width: 100%;
        left: 0;
        top: 0;
        // box-shadow: 0px 10px 100px rgba(0, 0, 0, 0.7);
    }
    .arrow{
        position: absolute;
        left: 50%;
        bottom: 50px;
        transform: translateX(-50%);
    }
    .nav {
        position: relative;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        padding: 15px 0;
        // background: linear-gradient(180deg, rgba(0, 0, 0, 0.8) 0%, rgba(0, 0, 0, 0) 100%);

        &-logo{
            display: flex;
            align-items: center;
            font-size: 22px;
            color: $yellow;
            img {
                margin-right: 10px;
            }
        }
        ul{
            display: flex;
            flex-direction: row;
            li {
                margin-right: 100px;
                list-style-type: none;
                &:nth-child(6){
                    margin-right: 0 !important;
                }
                a{
                    color: $yellow;
                    text-decoration: none;
                    font-size: 22px;
                }
            }
        }
    }
    .content {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        height: calc(100vh - 80px);
        width: 100%;
        position: relative;
        font-family: "Merriweather", sans-serif;
        font-weight: 400;
        .inner{
            display: flex;
            flex-direction: row;
            align-items: flex-start;
            justify-content: space-around;
            .lable, .description {
                color: $white;
            }
    
            .lable {
                font-size: 70px;
                letter-spacing: 0.15em;
                text-align: center;
                width: 40%;
            }
            .description{
                font-size: 30px;
                line-height: 60px;
                text-align: center;
                letter-spacing: 0.09em;
                width: 30%;
            }
        }
    }
}

.main{
    .who{
        padding: 100px 0;
        p {
            margin-top: 70px;
            padding: 0 200px;
        }
    }
    .help{
        position: relative;
        width: 100%;
        .content {
            position: relative;
            height: 100%;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            padding: 150px 0;
            .list {
                width: 70%;
                li{
                    margin-top: 50px;
                }
            }
        }
    }
    .what{
        padding: 100px 0;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        .list {
            width: 70%;
            margin-top: 70px;
        }
    }
    .why{
        position: relative;
        h2 {
            width: 70%;
        }
        p{
            margin-top: 70px;
        }
    }
    .how{
        padding: 100px 0;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        .content{
            padding: 70px;
            p{
                span{
                    padding: 8px 28px;
                    border: 5px solid $darkBlue;
                    border-radius: 50px;
                    background: rgba(231, 185, 22, 0.8);
                    color: $darkBlue;
                    cursor: pointer;
                }
            }
        }
        a{
            padding: 26px 180px;
            background: rgba(231, 185, 22, 0.8);
            border: 5px solid $darkBlue;
            box-sizing: border-box;
            box-shadow: 30px 30px 50px rgba(0, 0, 0, 0.5);
            backdrop-filter: blur(10px);
            border-radius: 50px;
            color: $darkBlue;
            font-size: 25px;
            font-weight: 800;
            text-decoration: none;
        }
    }
    .price{
        position: relative;
        width: 100%;
        .content{
            h3{
                font-size: 60px;
                font-family: "Merriweather", sans-serif;
                font-weight: 400;
                margin-top: 100px;
            }
            .price-list{
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: space-between;
                width: 70%;
                font-size: 30px;
                margin-top: 75px;
                .bor{
                    width: 100%;
                    height: 5px;
                    border-bottom: 5px dotted $white;
                    margin: 0 80px;
                }
            }
        }
    }
    .gift {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        p{
            width: 70%;
            margin-top: 70px;
        }
        .btn{
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            width: 70%;
            margin-top: 50px;
            span{
                color: $dark;
                font-size: 25px;
                font-weight: 400;
            }
            a{
                background: rgba(231, 185, 22, 0.8);
                border: 5px solid $darkBlue;
                box-shadow: 30px 30px 50px rgba(0, 0, 0, 0.5);
                backdrop-filter: blur(10px);
                border-radius: 50px;
                color: $darkBlue;
                font-size: 30px;
                padding: 25px 180px;
                text-decoration: none;
                cursor: pointer;
            }
        }
    }
}

.footer{
    position: relative;
    width: 100%;
    .content{
        padding: 190px 100px 0 100px;
        .top{
            display: flex;
            align-items: center;
            flex-direction: row;
            justify-content: space-between;
            width: 100%;
            p{
                color: $white;
                font-size: 30px;
                text-align: center;
                letter-spacing: 0.09em;
                line-height: 50px;
            }
            ul{
                display: flex;
                justify-content: space-between;
                flex-direction: row;
                align-items: center;
                li{
                    margin-right: 155px;
                    list-style-type: none;
                    width: 80px;
                    height: 80px;
                    &:nth-child(1){
                        width: 95px;
                    }
                    &:nth-child(4){
                        width: 75px;
                    }
                    &:nth-child(5){
                        margin-right: 0;
                    }
                }
            }
        }
        .btns{
            display: flex;
            align-items: center;
            justify-content: space-between;
            flex-direction: row;
            margin-top: 45px;
            a{
                padding: 23px 84px;
                font-size: 22px;
                letter-spacing: 0.09em;
                color: $yellow;
                background: rgba(231, 185, 22, 0.25);
                border: 5px solid $yellow;
                box-sizing: border-box;
                box-shadow: 30px 30px 50px rgba(0, 0, 0, 0.7);
                backdrop-filter: blur(10px);
                border-radius: 50px;
                text-decoration: none;
                margin-right: 50px;
                cursor: pointer;
                &:nth-child(3){
                    margin-right: 0;
                }
            }
        }
    }
}

.modal {
    display: none;
    position: fixed;
    z-index: 10;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(0,0,0);
    background-color: rgba(0,0,0,0.4);
    justify-content: center;
    align-items: center;
}
#qwM{
    .modal-content {
        background-color: #fefefe;
        padding: 104px 140px;
        .i-g-1{
            width: 100%;
            .input{
                width: 100%;
            }
        }
        .i-g-2{
            width: 100%;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            margin-top: 40px;
            div{
                margin-right: 40px;
                width: 50%;
                input{
                    width: 100%;
                }
                &:nth-child(2){
                    margin-right: 0;
                }
            }
        }
        .i-g-3{
            width: 100%;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            margin-top: 40px;
            div{
                width: 50%;
                #file{
                    width: 100%;
                }
                margin-right: 40px;
                &:nth-child(2){
                    margin-right: 0;
                }
                input{
                    height: 212px;
                }
                textarea{
                    height: auto;
                }
            }
        }
        .m-btns{
            width: 100%;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            margin-top: 40px;
            a{
                width: 50%;
                text-align: center;
                box-shadow: 30px 30px 50px rgba(0, 0, 0, 0.5);
                backdrop-filter: blur(10px);
                border-radius: 40px;
                padding: 18px 0;
                font-size: 30px;
                text-decoration: none;
                margin-right: 30px;
                cursor: pointer;
                &:nth-child(1){
                    background: rgba(67, 127, 151, 0.25);
                    border: 5px solid $green;
                    color: $green;
                }
                &:nth-child(2){
                    background: rgba(186, 45, 11, 0.25);
                    border: 5px solid $red;
                    color: $red;
                    margin-right: 0;
                }
            }
        }
    }
}

