*,
*::before,
*::after {
	box-sizing: border-box;
}

.flex {
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
}

.--just-space {
	justify-content: space-between;
}

.--just-center {
	justify-content: center;
}

.--just-end {
	justify-content: flex-end;
}

.--align-str {
	align-items: stretch;
}

.--align-center {
	align-items: center;
}

.--align-end {
	align-items: flex-end;
}

.--dir-col {
	flex-direction: column;
}

html {
	font-size: 16px;
}

html,
body {
	min-height: 100%;
	position: relative;
	font-family: "Nunito", sans-serif;
}

body {
	font-size: 1rem;
	background: #EEF4D4;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}

.container {
	max-width: 1640px;
	width: 100%;
	margin: 0 auto;
	padding: 0 15px;
}

.head {
	font-size: 70px;
    font-family: "Merriweather", sans-serif;
    letter-spacing: 0.09em;
    font-weight: 700;
    text-align: center;
}
.par {
	font-size: 35px;
	text-align: center;
}

.light{
	color: $white;
}

.dark {
	color: $dark;
}

.banner {
	width: 100%;
    position: absolute;
    top: 0;
    left: 0;
}
.content{
	position: relative;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 150px 0;
}

.input, textarea{
	border: 3px solid $dark;
    border-radius: 40px;
    outline: none;
    height: 80px;
    padding: 24px 40px;
}

label{
	font-size: 15px;
	margin-left: 20px;
	font-weight: 400;
}